.promo-card {
    max-width: 301px;
    width: 33%;
    height: 200px;
    border-radius: 10px;
background: url('../media/promo1.png');
background-size:contain;
background-repeat: no-repeat; 
margin-right: 20px;
    }

    .promo-card:not(:last-child) {
        margin-right: 20px;
    }

    
@media screen and (max-width: 850px) {
    .promo-card {
        max-width: 361px;
        width: 100%;
        position: relative;
        background: url('../media/promo-mobile.jpg');
        background-size: cover;
        background-repeat: no-repeat; 
        margin: 0px;
        height: 120px;
      }
}