.navbar {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  /* TOP NAVBAR */
  .navbar-top {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 30px;
    gap: 50px;
    height: 25px;
    background-color: #021724;
  }

  .navbar-top-item {
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content:flex-start;
    color: #fff;
  }

  .navbar-top-left-item {
    margin-left: auto;
    margin-right: 40px;
  }

  .navbar-top-item:hover, .navbar-top-left-item:hover {
    border-bottom: 1px solid #fff;
  }

  /* MIDDLE NAVBAR */
  .navbar-middle {
    display: flex;
    justify-content: space-between;
    height: 185px;
    background-color: #0B5077;
  }

  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35%;
  }

  .logo img {
    width: 150px;
    height: auto;
    margin-top:10px;
  }

  .search {
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 33px;
    position: relative;
  }

  .search-bar {
    width: 90%;
  }

  .search-input-container {
    width: 100%;
  }
  
  .search-input-container input {
    border-radius: 10px;
    height: 50px;
    width: 100%;
    box-sizing: border-box;
  }

  .search-bar input {
    border-radius: 10px;
    height: 50px;
    width: 100%;
    box-sizing: border-box;
  }

  .input-button-wrapper {
    position: relative;
  }
  
  .input-button-wrapper .btn {
    position: absolute;
    top: 5px;
    right: 5px;
  }
  .search-dropdown-hidden {
    display: none;
  }

  .search-dropdown-visible div {
    position: relative;
    font-size: 14px;;
    border-bottom: 1px solid gray;
    border-left: none;
    border-right: none;
    background-color: #fff;
    height: 20px;
    z-index: 1000;
    padding: 5px;
    cursor: pointer;
  }

  .search-dropdown-visible div:first-child {
    background-color:#FD7E14;
    color: white;
    pointer-events: none;
  }
  
  .search-dropdown-visible div:hover {
    background-color: lightgray;
    color: black;
    font-weight: bold;
    border: 1px solid black;
  }

  .search-by-department {
    background-color: #FFF;
    color: #0B5077;
    font-size: 12px;
    height: 32px;
    width: 180px;
    border-radius: 10px;
    margin-bottom: 5px;
    border-bottom: 2px solid #000;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 15px;
  }

  .search-options {
    display: flex;
    align-items: center;
    gap: 40px;
  }

  .selected-departments {
  color: #fff;
  }

  .department-dropdown {
    width: 640px;
    position: absolute;
    background-color: white;
    border: 1px solid #000;
    border-radius: 10px;
    padding: 10px;
    z-index: 1000;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    top: 22%;
    left:0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
  }
  .department-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .department-dropdown input[type="checkbox"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: white;
    border: 1px solid #000;
    width: 12px;
    height: 12px;
  }
  
  .department-dropdown input[type="checkbox"]:checked {
    background-color: white;
    background-image: url('../media/checkbox.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  .department-dropdown label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    cursor: pointer;
  }
  
  .department-dropdown label:hover {
    background-color: lightgray;
  }

  .department-name {
    font-size: 16px;
    color: #0B5077;
  }
  
  .department-item {
    font-size: 12px;
    color: black;
    margin-top: 10px;
  }

  .client-buttons {
    width: 35%;
    display: flex;
    justify-content: center;
    margin-top: 80px;
    gap: 40px;
  }

  .cart-counter {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    left: 130px;
    bottom: 50px;
    background-color: black;
    width: 15px;
    height: 20px;
    color: white;
    font-size: 12px;
    font-weight: 700;
    padding: 5px;
    border-radius: 10px;
    border: 1px solid #000;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  }

  /* BOTTOM NAVBAR */
  .navbar-bottom {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 30px;
    gap: 50px;
    border-radius: 10px 10px 0px 0px;
    height: 25px;
    background-color: #FD7E14;
    margin-top: -25px;
  }
  
  .navbar-bottom-item {
    background-color: transparent;
    border: 0;
    font-size: 16px;
  }

  .navbar-middle-hiden {
    display: none;
  }

  /* RESPONSIVE BREAKPOINTS */
  @media screen and (max-width: 1500px) {
    .logo {
      width: 20%;
    }

    .search {  
      margin-left: 0;
    }

    .client-buttons {
      width: 35%;
      margin-right: 0px;
      margin-top: 80px;
      gap: 20px;
      justify-content: flex-end;
      margin-right: 5%;
    }
  }
  @media screen and (max-width: 1380px) {
  .client-buttons {
    margin-right: 2%;
  }

  .search-bar {
    width: 100%;
  }
  }

  @media screen and (max-width: 1280px) {
    .search, .logo, .navbar-middle, .navbar-bottom, .search-dropdown-visible div:first-child {
      display: none;
    }

    .navbar-top {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding-left: 30px;
      gap: 50px;
      height: 70px;
      background-color: #0B5077;
      overflow-x: scroll; /* make it scrollable */
      white-space: nowrap; /* prevent words from wrapping */
      -ms-overflow-style: none;
      scrollbar-width: none;
    }
    
    .navbar-top::-webkit-scrollbar {
      display: none;
    }

    .navbar-top-item {
      font-size: 1em;
    }
    .navbar-top-left-item {
      align-items: center;
    }

    .navbar-middle-hiden {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: #0B5077;
      height: 70px;
    }

    .menu-search {
      display: flex;
      justify-content: flex-start;
      margin-left: 20px;
      align-items: center;
      gap: 30px;
      padding: 0;
    }
    
    .hidden-logo {
      width: 150px;
      
      display: flex;
      justify-content: center;
      align-items: center;
    }
  
    .hidden-logo img {
      width: 100px;
      height: auto;
      margin: auto;
    }
    .toggle-container {
      position: absolute;
      width: 234px;
      height: 0;
    }

    .toggle-visible {
      display: block;
      position: absolute;
      width: 234px;
      height: 448px;
      top: 50px;
      background-color: #FAFAFC;
      color: #0063F7;
      font-size: 16px;
      line-height: 3.2;
      font-weight: 400px;
      padding-left: 14px;
      padding-top: 25px;
      padding-bottom: 25px;
      border-radius: 10px;
      border: 1px solid #C2C2C2;
      background: var(--Side-bar-bg, #FAFAFC);
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
      transform: scaleX(1) scaleY(1);
      transform-origin: top left;
      transition: transform 0.5s ease-in-out, opacity 0.2s ease-in-out;
      opacity: 1;
      z-index: 9999;
      cursor: pointer;
    }

    .toggle-visible div::before {
      content: '• ';
      color: #0063F7;
    }
    
    .toggle-hidden {
      visibility: hidden;
      transform: scaleX(0) scaleY(0);
      transform-origin: top left;
      transition: transform 0.5s ease-in-out, opacity 0.2s ease-in-out, visibility 0s linear 0.5s;
      opacity: 0;
    }

    .toggle-visible div:first-child {
      font-size: 18px;
      text-decoration-line: underline;
    }

    .toggle-visible div:first-child::before,
    .toggle-visible div:last-child::before {
      content: '';
    }

    .client-buttons {
      gap: 20px;
      margin-top: 20px;
    }

  }

  /* FULL RESPONSIVE MODE*/
  @media screen and (max-width: 820px) {
    .navbar-bottom, .search, .logo, .navbar-middle, .mobile-hide, .cart-counter, .search-dropdown-visible div:first-child {
      display: none;
    }

    .client-buttons {
      margin-top: 0;
      display: flex;
      gap: 25px;
margin-right: 40px;
    }
  }