.dropdown-card {
    width: 170px;
    min-height: 155px;
    border-radius: 10px;
    opacity: var(--Quantity, 1);
    background: var(--Drop-down-info-bg, #EBEBF0);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    align-items: center;
    padding-left: 5px;
    margin-top: 5px;
    margin-bottom: 15px;
  }

  .dropdown-specs {
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
  }

  .dropdown-property {
font-size: 12px;
font-weight: 700;
line-height: 0;
margin: 0;
margin-right: 5px;
  }

  .dropdown-value {
    font-size: 11px;
font-weight: 400;
line-height: 0;
  }

  